<template>
<div class="buy">
  <div class="buyheader">
    <h1>产品分布图</h1>
  </div>
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>产品分布图</el-breadcrumb-item>
  </el-breadcrumb>
  <div class="map">
    <h4>阿莫西林胶囊</h4>
    <ve-bar :data="chartData1" :settings="chartSettings1"></ve-bar>
    <h5>同类产品排名</h5>
    <ve-heatmap :data="chartData" :settings="chartSettings"></ve-heatmap>
  </div>
  <div class="map">
    <h4>安儿宁颗粒</h4>
    <ve-bar :data="chartData1" :settings="chartSettings1"></ve-bar>
    <h5>同类产品排名</h5>
    <ve-heatmap :data="chartData" :settings="chartSettings"></ve-heatmap>
  </div>
</div>
</template>

<script>
import { buys } from "~/data";

export default {
  data() {
    return {
      buys: buys
    };
  },
  methods: {
    handleNodeClick(data) {
      console.log(data);
    }
  },
  created: function() {
    this.chartData = {
      columns: ["lat", "lng", "人数"],
      rows: [
        { lat: 115.892151, lng: 28.676493, 人数: 1000 },
        { lat: 117.000923, lng: 36.675807, 人数: 400 },
        { lat: 113.665412, lng: 34.757975, 人数: 800 },
        { lat: 114.298572, lng: 30.584355, 人数: 200 },
        { lat: 112.982279, lng: 28.19409, 人数: 100 },
        { lat: 113.280637, lng: 23.125178, 人数: 300 },
        { lat: 110.33119, lng: 20.031971, 人数: 800 },
        { lat: 104.065735, lng: 30.659462, 人数: 700 },
        { lat: 108.948024, lng: 34.263161, 人数: 300 },
        { lat: 103.823557, lng: 36.058039, 人数: 500 }
      ]
    };
    this.chartSettings = {
      position: "china",
      type: "map",
      geo: {
        label: {
          emphasis: {
            show: false
          }
        },
        itemStyle: {
          normal: {
            areaColor: "#461b93",
            borderColor: "#461b93"
          },
          emphasis: {
            areaColor: "#2a333d"
          }
        }
      }
    };
    this.chartData1 = {
      columns: ["产品", "销售数量", "药店数量", "医院数量"],
      rows: [
        { 产品: "阿莫西林胶囊", 销售数量: 1500, 药店数量: 800, 医院数量: 90 },
        { 产品: "阿莫西林颗粒", 销售数量: 2100, 药店数量: 300, 医院数量: 80 },
        { 产品: "骨气颗粒", 销售数量: 2200, 药店数量: 800, 医院数量: 73  },
        { 产品: "安儿灵颗粒", 销售数量: 2340, 药店数量: 800, 医院数量: 65  },
        { 产品: "去痛颗粒", 销售数量: 2400, 药店数量: 900, 医院数量: 90  },
        { 产品: "安安颗粒", 销售数量: 2500, 药店数量: 700, 医院数量: 209  }
      ]
    };
    this.chartSettings1 = {
        xAxisType: ['KMB', 'percent'],
        xAxisName: ['成本', '利润'],
        axisSite: {
          top: ['利润']
        }
      }
  }
};
</script>

<style lang="scss" scoped>
.el-tree {
  padding: 0 15px 15px 15px;
}

.map {
  margin: 0 15px 15px 15px;
  padding: 15px;
  border-radius: 3px;
  background: #f5f7fa;
  border: 1px solid #eee;
  h4 {
    text-align: center;
    padding: 7px;
    font-weight: 400;
  }
  h5 {
    text-align: center;
    font-weight: 300;
  }
}
</style>



